.game h1 {
  font-size: 2.5em;
}

.points span {
  font-weight: bold;
}

.tip span {
  color: rgb(0, 0, 111);
}

.wordContainer {
  margin: 1.5em;
  padding: 1.5em;
  border: 20px solid rgb(0, 0, 111);
  display: flex;
}

.letter, .blankSquare {
  font-size: 70px;
  line-height: 1.5;
  border: 3px solid black;
  height: 100px;
  width: 100px;
  text-transform: uppercase;
  background-color: white;
  color: black;
  font-weight: bold;
  margin-right: 2px;
}

.letterContainer p {
  margin-bottom: 1.2em;
}

.letterContainer form {
  display: flex;
  align-items: center;
  justify-content: center;
}

.letterContainer input {
  height: 50px;
  width: 50px;
  text-align: center;
  margin-right: 1em;
}